<template>
  <div class="outter">
    <input @change="onChange" type="file" id="changeMore">
    <div class="box">
      <div class="left">
        圆角： <input v-model="radius" type="text">
        边框颜色: <input v-model="borderColor" type="text">
        阴影颜色: <input v-model="shadowColor" type="text">
        边框厚度: <input v-model="borderWidth" type="text">
        阴影厚度: <input v-model="shadowWidth" type="text">
        虚化程度: <input v-model="shadowRadius" type="text">
      </div>
      <div>
        <img class="img" :style="styles" :src="pictureUrl" width="210" height="350" alt="">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LayoutDoubleFlyingWing',
  data() {
    return {
      pictureUrl: '',
      radius: 8,
      borderColor: '#2967a1',
      shadowColor: '#2967a1',
      borderWidth: 2,
      shadowWidth: 6,
      shadowRadius: 2
    }
  },
  computed: {
    styles() {
      const {shadowColor,borderWidth, shadowWidth,  shadowRadius, borderColor, radius} = this
      return {
        'box-shadow': `0 0 ${shadowWidth}px ${shadowRadius}px ${shadowColor}`,
        'border': `${borderWidth}px solid ${borderColor}`,
        'border-radius': `${radius}px`
      }
    }
  },
  methods: {
    onChange(e) {
      console.log(e)
      const file = e.target.files[0] //拿到原始对象
      const reader = new FileReader()

      reader.readAsDataURL(file)

      //文件加载成功以后，渲染到页面
      reader.onload = (e) => {
        console.log(typeof e.target.result)
        this.pictureUrl = e.target.result

        console.log(this.pictureUrl)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.img {
}
.outter {
  margin: 24px;
}
.box {
  display: flex;
  .left {
    margin-right: 120px;
    input {
      display: block;
      width: 200px;
      margin-top: 8px;
    }
  }
}

</style>